<template>
  <div>
    <a-form style="margin: 40px auto 0;">
      <result title="发布成功" :is-success="true" style="max-width: 560px;">
        <div class="information">
          <a-steps :current="1">
            <a-popover slot="progressDot" slot-scope="{index, status, prefixCls}">
              <template slot="content">
                <span>step {{ index }} status: {{ status }}</span>
              </template>
              <span :class="`${prefixCls}-icon-dot`"></span>
            </a-popover>
            <a-step title="发布需求"/>
            <a-step title="匹配人数"/>
            <a-step title="匹配完成待支付"/>
            <a-step title="交易成功"/>
          </a-steps>
        </div>
        <div slot="action">
          <a-button type="primary" @click="finish">继续发布</a-button>
          <a-button style="margin-left: 8px" @click="toOrderList">查看需求</a-button>
        </div>
      </result>
    </a-form>
  </div>
</template>

<script>
import Result from '@/components/Result'

export default {
  name: 'DemandSucceed',
  components: {
    Result
  },
  methods: {
    finish () {
      this.$router.push('/demand')
    //   this.$emit('finish')
    },
    toOrderList () {
      this.$router.push('/listpage')
    }
  }
}
</script>

<style scoped>

</style>



